<template>
  <div class="square">
    <!-- <div class="header">
      <img class="header_img" src="../../assets/images/left.png" alt />
      <span class="header_text">追问详情</span>
    </div>-->
    <!-- <van-nav-bar            
        left-arrow
        title="追问详情"
        @click-left="onClickLeft"
    >    
    </van-nav-bar>-->
    <div class="item">
      <div class="list" v-for="(item,index) in list" :key="index">
        <!-- <span class="content_time" v-show="item.dateline == item.dateline"></span> -->
        <!-- <span
          class="content_time"
          v-show="item.dateline !== item.dateline"
        >{{item.dateline | timeformat()}}</span> -->
        <div class="content">
          <div class="right" v-show="item.right == 1">
            <img :src="item.avatar" class="right_avatar" alt />
            <span class="right_text" v-show="item.message.substring(0,4) != 'http'">{{item.message}}</span>
            <div class="right_text" v-show="item.message.substring(0,4) == 'http'">
              <img
                @click="answersImageClick(item.message,index)"
                :src="item.message"
                class="right_img"
                alt
              />
            </div>
          </div>
          <div class="left" v-show="item.right == 0">
            <span class="left_text" v-show="item.message.substring(0,4) != 'http'">{{item.message}}</span>
            <div class="left_text" v-show="item.message.substring(0,4) == 'http'">
              <img
                @click="answersImageClick(item.message,index)"
                :src="item.message"
                class="right_img"
                alt
              />
            </div>
            <img :src="item.avatar" class="left_avatar" alt />
          </div>
        </div>
      </div>
      <div class="input_item" id="id" v-show="data.is_can_reply == 1">
        <van-field
          class="input_item_textarea"
          @focus="animateWidth('company_name','blur')"
          @blur="blurClick"
          v-model="postParameter.message"
          placeholder="补充答案~"
          :border="false"
          @keyup.enter="addPost"
        >
          <span slot="button" class="input_content_button" @click="addPost">发布</span>
        </van-field>
        <img
          class="expression_ima"
          @click="getSmileyClick"
          src="../../assets/images/icon_expression.png"
          alt
        />
        <div class="input_item_img">
          <input type="file" class="file" @change="imageChange($event)" />
        </div>
      </div>
      <!--表情-->
      <div class="expression" v-show="expressionShow">
        <img
          @click="expressionReplyClick(item6.show)"
          class="expression_image"
          :src="item6.show"
          v-for="(item6,indexd) in expression"
          :key="indexd"
          alt
        />
      </div>
    </div>
  </div>
</template>
<script>
import { Toast, ImagePreview } from "vant";
import { getFromNom } from "@js/moment";
export default {
  data() {
    return {
      item: {
        token: this.$store.state.userData,
        tid: this.$route.query.tid,
        pid: this.$route.query.pid
      },
      list: [],
      data: {},
      aid_list: [],
      postParameter: {
        token: this.$store.state.userData,
        tid: "",
        pid: "",
        aid_list: "",
        attachment: 0,
        message: ""
      },
      expression: [],
      ImagePreviewDialog: "",
      expressionShow: false //表情
    };
  },
  mounted() {
    document.title = "追问详情";
    this.init();
    this.getSmiley();
  },
  methods: {
    getSmiley() {
      this.api.getSmiley().then(res => {
        if (res.err_code == 0) {
          this.expression = res.data.common_smiley[0].list;
        }
      });
    },
    //点击表情取消聚焦
    getSmileyClick() {
      this.expressionShow = true;
      let id = document.getElementById("id");
      console.log(console.log(id));
      id.style.bottom = "5.33333rem";
    },
    expressionReplyClick(message) {
      this.postParameter.pid = this.$route.query.pid;
      this.postParameter.tid = this.$route.query.tid;
      this.postParameter.attachment = 0;
      this.postParameter.message = message;
      this.postParameter.aid_list = "";
      console.log(this.postParameter);
      this.api.addQuestionReply(this.postParameter).then(res => {
        Toast.clear();
        if (res.err_code == 0) {
          this.$toast("回答成功！");
          this.postParameter.message = "";
          this.init();
          this.expressionShow = false;
          let id = document.getElementById("id");
          id.style.bottom = "0";
        }
      });
    },
    //查看图片
    answersImageClick(image, index) {
      let list = [];
      list.push(image);
      console.log(list);
      this.ImagePreviewDialog = ImagePreview({
        images: list,
        loop:false
      });
    },
    init() {
      this.api.getDialogue(this.item).then(res => {
        if (res.err_code == 0) {
          this.data = res.data;
          for (let i = 0; i < res.data.dialogue.length; i++) {
            console.log(res.data.dialogue[i].message.substring(0, 4));
            let a = this.GMTToStr(res.data.dialogue[i].dateline);
          }
          this.list = res.data.dialogue;
        }
      });
    },
    imageChange(v) {
      let formData = new FormData();
      formData.append("token", this.$store.state.userData);
      formData.append("myfile", v.target.files[0]);
      formData.append("type", "question");
      this.api.addImage(formData).then(res => {
        if (res.err_code == 0) {
          this.aid_list.push(res.data.aid);
          this.postParameter.pid = this.$route.query.pid;
          this.postParameter.tid = this.$route.query.tid;
          this.postParameter.attachment = 2;
          this.postParameter.message = res.data.read_file_url;
          this.postParameter.aid_list = res.data.aid;
          console.log(this.postParameter);
          this.api.addQuestionReply(this.postParameter).then(res => {
            Toast.clear();
            if (res.err_code == 0) {
              this.$toast("回答成功！");
              this.postParameter.message = "";
              this.init();
            }
          });
        }
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    // 输入框获取焦点
    animateWidth() {
      // setTimeout(function(){
      window.scrollTo(0, document.body.scrollHeight);
      // this.inputShow = false;
      // },300);
    },
    blurClick() {},
    // 回复
    addPost() {
      // this.inputShow = true;
      // if (!this.$store.state.userData) {
      //   window.location.href = "teldapp://login";
      //   return null;
      // }
      if (!this.postParameter.message) {
        return Toast("输入不能为空");
      }
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      this.postParameter.pid = this.$route.query.pid;
      this.postParameter.tid = this.$route.query.tid;
      this.api.addQuestionReply(this.postParameter).then(res => {
        Toast.clear();
        if (res.err_code == 0) {
          this.$toast("回答成功！");
          this.postParameter.message = "";
          this.init();
        }
      });
    },
    GMTToStr(time) {
      var date;
      if (time) {
        date = new Date(time);
      } else {
        date = new Date();
      }
      var nowYear = date.getFullYear();
      var nowMonth =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var nowDay = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return nowYear + "-" + nowMonth + "-" + nowDay;
    }
  },
  filters: {
    timeformat(v) {
      if (v) {
        return getFromNom(v * 1000);
      } else {
        return "";
      }
    }
  }
};
</script>
<style lang="less" scoped rel="stylesheet/less">
.item {
  margin-top: 70px;
}
.square {
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
  .list {
    padding: 15px 15px 80px 15px;
  }
  .header {
    height: 63.5px;
    display: flex;
    justify-content: end;
    align-items: flex-end;
    border-bottom: 0.5px solid #888888;
    padding-bottom: 11px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    .header_img {
      width: 21px;
      height: 21px;
      margin-left: 15px;
    }
    .header_text {
      font-size: 18px;
      color: #000000;
      margin-left: 115px;
    }
  }
}
.list {
  display: flex;
  flex-direction: column;
  align-items: center;
  .content_time {
    font-size: 9px;
    color: #cacaca;
    padding: 0 0 10px 0;
  }
  .content {
    display: flex;
    flex-direction: column;
    width: 100%;
    .right {
      display: flex;
      justify-content: flex-start;
      .right_text {
        font-size: 12px;
        color: #ffffff;
        background-color: #e5e5e5;
        border-top-right-radius: 15px;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        padding: 14px 22.5px;
        margin: 21px 0 0 6.5px;
        word-break: break-word;
      }
      .right_avatar {
        width: 42px;
        height: 42px;
        border-radius: 50%;
        border: 1px solid;
      }
      .right_img {
        width: 120px;
        height: 120px;
      }
    }
    .left {
      display: flex;
      justify-content: flex-end;
      // margin-top: 20px;
      .left_text {
        font-size: 12px;
        color: #ffffff;
        background-color: #0ec6f1;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        padding: 14px 22.5px;
        margin: 21px 6.5px 0 0;
        word-break: break-all;
        .right_img {
          width: 120px;
          height: 120px;
        }
      }
      .left_avatar {
        width: 42px;
        height: 42px;
        flex: none;
        border-radius: 50%;
        border: 1px solid;
      }
    }
  }
}
.input_item {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 50.5px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  align-items: center;
  .input_item_textarea {
    width: 250px;
    height: 31.5px;
    background-color: #d0d0d0;
    font-size: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    text-indent: 12.5px;
    margin-left: 25px;
  }
  .input_item_img {
    width: 27px;
    height: 27px;
    margin-left: 20px;
    background-image: url("../../assets/images/icon_image.png");
    background-size: 100% 100%;
    .file {
      opacity: 0;
      width: 27px;
      height: 27px;
      border-radius: 50%;
    }
  }
  .expression_ima {
    width: 27px;
    height: 27px;
    margin-left: 10px;
  }
}
.expression {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 200px;
  overflow: auto;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #ffffff;
  .expression_image {
    width: 50px;
    height: 50px;
    margin: 5px;
  }
}
</style>